@font-face {
  font-family: "Cyborg";
  src: url("../fonts/Cyborg/Cyborg.ttf") format("truetype"), url("../fonts/Cyborg/Cyborg.otf") format("opentype");
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: "Exposapce";
