.matrix {
  position: fixed;
  width: 97%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-between;
  pointer-events: none;
  background-image: linear-gradient(rgba(238, 238, 238, 0.0666666667) 2px, transparent 2px), linear-gradient(90deg, rgba(238, 238, 238, 0.0666666667) 2px, transparent 2px), linear-gradient(rgba(238, 238, 238, 0.0666666667) 1px, transparent 1px), linear-gradient(90deg, rgba(238, 238, 238, 0.0666666667) 1px, rgba(0, 0, 0, 0) 1px);
  background-position: -2px -2px, -2px -2px, -1px -1px, -1px -1px;
  background-size: 250px 250px, 250px 250px, 50px 50px, 50px 50px;
  border-radius: 0px;
  opacity: 0.5;
}
@media (max-width: 47.99875em) {
  .matrix {
    opacity: 0.2;
  }
}
.matrix p {
  line-height: 1;
}
.matrix span {
  display: block;
  width: 0.0625rem;
  height: 2dvh;
  font-size: 1rem;
  color: rgba(155, 255, 155, 0.0666666667);
  text-align: center;
  font-family: "Helvetica Neue", Helvetica, sans-serif;
}

:root {
  --Xcol: 139 ;
}

.loading-screen {
  position: fixed;
  width: 100%;
  height: 100dvh;
  top: 0;
  left: 0;
  z-index: 30;
  background-color: var(--colGreen);
  color: #000;
}

:root {
  --digitSize: 150px;
}

.counter {
  position: absolute;
  left: 3.125rem;
  bottom: 3.125rem;
  display: flex;
  height: var(--digitSize);
  font-size: var(--digitSize);
  clip-path: polygon(0 0, 100% 0, 100% var(--digitSize), 0 var(--digitSize));
  color: var(--colBlack);
}

.counter-1, .counter-2, .counter-3 {
  position: relative;
}

.num1offset1 {
  position: relative;
  right: -1.5625rem;
}

.num1offset2 {
  position: relative;
  right: -0.625rem;
}

.home .eye-block {
  position: fixed;
  top: 0;
  left: 0;
  width: calc(100% + 5rem);
}
@media (max-width: 61.99875em) {
  .home .eye-block {
    position: absolute;
  }
}
.home .eye-animation {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 3.125rem;
}
.home .eye-animation:hover .eye-smile {
  border-radius: 0% 0% 0% 0%/0% 0% 0% 0%;
}
@media (max-width: 61.99875em) {
  .home .eye-animation:hover .eye::after {
    transform: translate(50%, 50%);
  }
}
.home .eye-animation .eye-smile {
  width: 7.5rem;
  height: 60px;
  border-radius: 0% 0% 50% 50%/40% 40% 40% 40%;
  transition: all 0.3s;
  border: 5px solid #fff;
  border-top: none;
  border-inline: none;
  margin-top: -1.25rem;
}
.home .eyebrow_container {
  display: flex;
  gap: 3.125rem;
}
.home .eyebrow_container .eye_brow {
  width: 3.125rem;
  height: 0.3125rem;
  border-radius: 10px;
  background-color: #fff;
  margin-bottom: 1.25rem;
}
.home .eyebrow_container .left {
  rotate: -20deg;
  border-bottom-left-radius: 0px;
  border-top-left-radius: 90px;
}
.home .eyebrow_container .right {
  rotate: 20deg;
  border-bottom-right-radius: 0px;
  border-top-right-radius: 90px;
}
.home .eye_container {
  gap: 10px;
  display: flex;
}
.home .eye_container .eye {
  position: relative;
  display: inline-block;
  border-radius: 50%;
  height: 60px;
  width: 60px;
  background: #fff;
  box-shadow: inset 0 0 0.625rem rgb(0, 0, 0);
  transition: scale 0.2s, background-color 0.5s, box-shadow 0.5s;
}
.home .eye_container .eye:hover {
  background-color: #ff0000;
}
.home .eye_container .eye:active {
  scale: 0.8;
}
.home .eye_container .eye::after {
  content: " ";
  position: absolute;
  bottom: 5%;
  right: 50%;
  width: 1.25rem;
  height: 1.25rem;
  background: #000;
  border-radius: 50%;
  border: 0.3125rem solid #2e536f;
  pointer-events: none;
  transition: 0.5s;
}
@media (max-width: 61.99875em) {
  .home .eye_container .eye::after {
    bottom: 50%;
    transform: translate(50%, 120%);
  }
}

.cursor {
  display: none;
}
.cursor._active {
  mix-blend-mode: darken;
}
.cursor._active::after {
  width: 6.25rem;
  height: 6.25rem;
  border-color: #000;
